:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --content-width: 1470px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --bg-color: #fcf8e3;
  --black-color: #000;
  --primary-red: #ff0100;
  --primary-color: #102842;
  --grey-color: #f5f5f5;

  --border-radius: 5px;
}
