html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
p,
ul,
ol {
  margin: 0;
  padding: 0;
}

.page {
  overflow-x: hidden;
  height: 100%;
}

.page__body {
  position: relative;
  overflow-x: hidden;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: var(--font-family);
  font-weight: var(--regular-font);
  font-size: 16px;
  background-color: var(--light-color);
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  min-height: 100vh;
}

.main {
  position: relative;
  flex: 1 1 auto;
}

.is-hidden {
  display: none !important;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
}

.centered {
  text-align: center;
}

.page--ios .dis-scroll {
  position: relative;
}

.section {
  padding: 80px 0;

  h3 {
    margin-bottom: 30px;
    font-size: clamp(1.1rem, 1.396rem + 0.52vw, 1.3rem);
    font-weight: 500;
    line-height: 140%;
    text-wrap: balance;
  }

  h4 {
    margin-bottom: 5px;
    font-size: 14px;
    font-weight: 300;
    text-wrap: balance;
    line-height: 1;
    text-transform: uppercase;
    color: var(--primary-red);
  }

}

@media screen and (max-width:1024px) {
  .section {
    padding: 50px 0;
  }
}
