.vessel {
  &__item {
    border-radius: var(--border-radius);
    max-width: 1200px;
    margin: 0 auto;
    padding: 15px 10px;
    background-color: var(--grey-color);

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &__list {
    &-item {
      display: flex;
      gap: 15px;
      border-bottom: 1px solid var(--primary-color);
      padding: 15px 0;
    }
  }

  h2 {
    position: relative;
    margin-bottom: 15px;
    padding-bottom: 15px;
    font-size: clamp(1.5rem, 1.396rem + 0.52vw, 1.875rem);
    font-weight: 500;
    line-height: 140%;
    text-wrap: balance;
    color: var(--primary-color);

    span {
      font-size: clamp(2rem, 1.396rem + 0.52vw, 2.5rem);
      font-weight: 700;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100px;
      height: 6px;
      /* Подбери нужную высоту */
      background-color: var(--primary-red);
      clip-path: polygon(10% 100%, 100% 100%, 100% 0%, 0% 0%);
    }
  }

  p {
    font-size: clamp(1rem, 1.396rem + 0.52vw, 1.1rem);
    font-weight: 300;
    line-height: 140%;
    text-wrap: balance;
    color: var(--primary-color);
  }

  strong {
    color: var(--primary-color);
    font-weight: 500;
  }
}

.activity {
  &__list {
    display: flex;
    flex-direction: column;
    gap: 15px;

    li {
      position: relative;

      p {
        padding-left: 10px;
      }
    }
  }

  &__info {
    width: 50%;
  }

  &__pic {
    width: 50%;
    height: 300px;

    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}

@media screen and (max-width:767px) {
  .vessel {
    &__list {
      &-item {
        flex-direction: column;
      }
    }
  }
  .activity {
    &__info,
    &__pic {
      width: 100%;
    }
  }
}
